import React from "react";
import { useAccount, useDisconnect, useNetwork } from "wagmi";
import { Web3Button } from "@web3modal/react";
import "./App.css";

function App() {
  const { address, isConnected } = useAccount(); // To get the wallet address and connection status
  const { chain } = useNetwork(); // To get the network details
  const { disconnect } = useDisconnect(); // To allow disconnection

  return (
    <div className="App">
      <h2>Web3Modal React Connection</h2>
      <div className="wallet-container">
        {isConnected ? (
          <div>
            <h3>Wallet Details</h3>
            <p><strong>Address:</strong> {address}</p>
            <p><strong>Chain ID:</strong> {chain?.id}</p>
            <p><strong>Network Name:</strong> {chain?.name}</p>
            <button onClick={() => disconnect()}>Disconnect</button>
          </div>
        ) : (
          <Web3Button balance="show" icon="hide" label="Connect Wallet" />
        )}
      </div>
    </div>
  );
}

export default App;
